import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Toolbar from "@mui/material/Toolbar";

const dispatchedEvents = [];

const SrsMuiToolbar = function (props, ref) {
  return (
    <Toolbar {...props} theme={props.muiTheme}>
      <slot />
    </Toolbar>
  );
};

const styles = `
  /* Custom styles for the MuiToolbar component */
`;

const props = [
  {
    name: "disableGutters",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "variant",
    defaultValue: "regular",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Toolbar if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiToolbar),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
